section#About{
    background-color: var(--black);
    display: grid;
    grid-template-columns: 45% 45% 10%;
    grid-template-rows: 20% 80%;
    place-items: center;
    h1{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: end;
        user-select: none;
        grid-column-start: 1;
        grid-column-end: 4;
        color: var(--white);
    }
    div.cardList{
        width: 100%;
        height: 100%;
        display: grid;
        place-items: center;
        grid-template-columns: 50% 30%;
        padding: 2%;
        margin-left: 20%;
        grid-template-rows: 50% 50%;
    }
    div.infoContainer{
        width: 90%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: var(--white);
        font-family: regularText;
        font-size: 1.2rem;
        line-height: 2rem;
        gap: 3rem;
        tbody{
            width: 90%;
            tr{
                vertical-align: top;
                td{
                    color: var(--white);
                    align-items: start;
                    justify-content: start;
                }
                td:first-child{
                    color: var(--categoryColor);
                    user-select: none;
                }
                td:nth-child(2){
                    color: var(--primary);
                    display: flex;
                    margin: 0 3rem;
                    user-select: none;
                }
            }
        }
        a{
            color: var(--white);
            text-decoration: none;
            display: flex;
            font-size: 1.4rem;
            border: none;
            padding: .5rem 1.7rem;
            border-radius: 2rem;
            margin-top: .5rem;
            user-select: none;
            position: relative;
            &:hover {
                background-color: transparent;
                box-shadow: none;
                color: var(--primary);
              }
              &::before {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                border: 3px solid var(--white);
                transition: opacity 0.3s, border 0.3s;
              }
              &:hover::before {
                opacity: 0;
              }
              &::after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: transparent;
                border: 3px solid var(--primary);
                opacity: 0;
                z-index: 1;
                transform: scaleX(1.1) scaleY(1.3);
                transition: transform 0.3s, opacity 0.3s;
              }
              &:hover::after {
                opacity: 1;
                transform: scaleX(1) scaleY(1);
              }
        }
    }
}